import { Component, ViewChild,  ElementRef} from '@angular/core';
import { IonicPage, NavController, Nav } from 'ionic-angular';
import { TabsPage } from '../tabs/tabs.page';
import { LoginPage } from '../login/login.page';
import { RegistrationPage } from '../registration/registration.page';
import { HomePage } from '../home/home.page';
import { AboutPage } from '../about/about.page';

export interface PageInterface {
  title: string;
  pageName: string;
  tabComponent?: any;
  index?: number;
  icon: string;
}

@IonicPage()
@Component({
  selector: 'app-menu',
  templateUrl: './menu.page.html',
  styleUrls: ['./menu.page.scss'],
})
export class MenuPage {
  // Basic root for our content view
  rootPage = 'TabsPage';

  //Reference to the app's root nav
  @ViewChild(Nav, { static: true }) nav: Nav;

  pages: PageInterface[] = [
    { title: 'Login', pageName: 'TabsPage', tabComponent: 'LoginPage', index: 0, icon: 'login' },
    { title: 'Registration', pageName: 'TabsPage', tabComponent: 'RegistrationPage', index: 1, icon: 'registration' },
    { title: 'About', pageName: 'AboutPage', tabComponent: 'AboutPage', index: 2, icon: 'about' },
    { title: 'Home', pageName: 'HomePage', icon: 'shuffle' }
  ]

  constructor( public navCtrl: NavController ) { }

  openPage(page: PageInterface) {
    let params = {};

    // The index is equal to the order of our tabs inside tabs.ts
    if (page.index) {
      params = { tabIndex: page.index };
    }

    // The active child nav is our Tabs Navigation
    if (this.nav.getActiveChildNav() && page.index != undefined) {
      this.nav.getActiveChildNav().select(page.index);
    } else {
      // Tabs are not active, so reset the root page 
      // In this case: moving to or from SpecialPage
      this.nav.setRoot(page.pageName, params);
    }
  }

  isActive(page: PageInterface) {
    // Again the Tabs Navigation
    let childNav = this.nav.getActiveChildNav();
 
    if (childNav) {
      if (childNav.getSelected() && childNav.getSelected().root === page.tabComponent) {
        return 'primary';
      }
      return;
    }
 
    // Fallback needed when there is no active childnav (tabs not active)
    if (this.nav.getActive() && this.nav.getActive().name === page.pageName) {
      return 'primary';
    }
    return;
  }

}
